.page {
  position: relative;
  max-width: 800px;
  width: 60%;
  margin: 0 auto;

  [data-header-desktop] & {
    padding-top: $header-height;
  }

  [data-header-desktop=normal] & {
    padding-top: 0;
  }

  @include blur;
}

.page-post {
  position: relative;
  max-width: 100%;
  width: 70%;
  margin: 0 auto;

  &[data-toc=disable] {
    margin: 0 auto;
  }

  [data-header-desktop] & {
    padding-top: $header-height;
  }

  [data-header-desktop=normal] & {
    padding-top: 0;
  }

  @include blur;
}

.single-card {
  padding: 1rem;
  margin: 1rem 0;
  @include border-radius(.5rem);
  background-color: $global-background-card-color;
  [theme=dark] & {
    background-color: $global-background-card-color-dark;
  }
  &[data-image=true] {
    margin-top: 0;
    @include border-radius(0 0 .5rem .5rem);
  }
}

@import "_single";
@import "_special";
@import "_archive";
@import "_home";
@import "_404";
