.home {
  .home-profile {
    @include transform(translateY( 16vh));
    padding: 0 0 .5rem;
    text-align: center;

    .home-avatar {
      padding: .5rem;

      img {
        background-color: #afc6e9;
        display: inline-block;
        width: 8rem;
        height: auto;
        margin: 0 auto;
        @include border-radius(100%);
        @include box-shadow(0 0 0 .3618em rgba(0, 0, 0, .05));
        @include transition(all 0.4s ease);
        [theme=dark] & {
          background-color: #f2f2f2;
        }
        &:hover {
          position: relative;
          @include transform(translateY(-.75rem));
        }
      }
    }

    .home-title {
      font-size: 1.25rem;
      font-weight: bold;
      margin: 0;
      padding: .1rem;
    }

    .home-subtitle {
      font-size: 1rem;
      font-weight: normal;
      margin: 0;
      padding: .1rem;
    }

    .links {
      padding: .5rem;
      font-size: 1.5rem;

      a * {
        vertical-align: text-bottom;
        padding: 0 .25rem;
      }

      img {
        height: 1.5rem;
        padding: 0 .25rem;
      }
    }

    .home-disclaimer {
      font-size: 1rem;
      line-height: 1.5rem;
      font-weight: normal;
      margin: 0;
      padding: .5rem;
      color: $global-font-secondary-color;

      [theme=dark] & {
        color: $global-font-secondary-color-dark;
      }
    }
  }
}

.home[data-home=posts] {
  .home-profile {
    @include transform(translateY(0));
    padding-top: 2rem;
  }

  .home-avatar img {
    width: 6rem;
  }

  .summary {
    background-color: $global-background-card-color;
    padding-bottom: .8rem;
    margin-bottom: 1rem;
    @include border-radius(.5rem);
    color: $global-font-color;
    @include transition(transform 0.4s ease);

    &:hover {
      @include transform(scale(1.01));
    }

    [theme=dark] & {
      background-color: $global-background-card-color-dark;
      color: $global-font-color-dark;
    }

    .featured-image-preview {
      width: 100%;
      padding: 30% 0 0;
      position: relative;
      margin: 0 auto;

      img {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        @include object-fit(none);
        @include border-radius(.5rem .5rem 0 0);
      }

      img.lazyloaded {
        @include object-fit(cover);
      }
    }

    .single-title {
      font-size: 1.25rem;
      line-height: 140%;
      margin: 0.4rem 1rem;
      padding-top: 1rem;
    }

    .post-meta {
      margin: 0 1rem;
    }

    p {
      margin: .5rem 0;
    }

    .content {
      @include box(vertical);
      -webkit-line-clamp: 3;
      margin: .3rem 1rem 0 1rem;
      overflow: hidden;
      text-overflow: ellipsis;
      @include overflow-wrap(break-word);
      color: $global-font-secondary-color;

      [theme=dark] & {
        color: $global-font-secondary-color-dark;
      }

      h2,
      h3,
      h4,
      h5,
      h6,
      p {
        font-size: 1rem;
        line-height: 1.5;
        display: inline;

        &::after {
          content: "\A";
          white-space: pre;
        }
      }

      h2 {
        font-size: 1.125rem;
      }

      @include link(false, true);

      b, strong {
        color: $global-font-secondary-color;

        [theme=dark] & {
          color: $global-font-secondary-color-dark;
        }
      }
    }

    .post-footer {
      margin: .4rem 1rem 0 1rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: .875rem;

      @include link(false, false);

      .post-tags {
        margin-top: 6px;
        @include link(true, true);
      }
        .border-tags {
          padding: 6px 12px;
          margin-right: 6px;
          @include border-radius(20px);
          border: 1px solid $global-font-secondary-color;
          &:hover {
            background-color: $global-background-color;
          }
          [theme=dark] & {
            border: 1px solid $global-font-secondary-color-dark;
            &:hover {
              background-color: $global-background-color-dark;
            }
          }
        }
    }
  }
}
